
import { defineComponent, ref } from "vue";
import { emitter } from "./todoList.vue";
import "../css/lit-animation.css";

export default defineComponent({
  name: "todoModal",
  props: {
    inputVal: String
  },
  setup() {
    const inputRef = ref("");
    const newTask = () => {
      emitter.emit("list-created", inputRef.value);
      inputRef.value = "";
    };
    return {
      inputRef,
      newTask
    };
  }
});
